import React, { useState } from "react";

let colors = ['red', 'green', 'blue', 'yellow', 'darkorange', 'purple', 'black', 'darkgrey', 'white', 'lightgrey'];
let chosen = 6;
let row0 = 7;

const slots0 = new Array((row0+1)*4).fill(6);

const matches0 = new Array((row0+1)*4).fill(9);

const buttons = [
    {id: 1, name: 'New Game'},
    {id: 2, name: 'Check'},
];

const balls = [
  {idx: 5}, {idx: 4}, {idx: 3}, {idx: 2}, {idx: 1}, {idx: 0},
];

const randomCode = () => {
    let rcode = [0, 0, 0, 0];
    for (let i=0; i < 4; i++) {
        rcode[i] = Math.floor(Math.random()*6);
    }
    return rcode;
}

let code0 = randomCode();

const matchCheck = (guess, code) => {
    let fullMatch = 0;
    for (let i=0; i < 4; i++) {
        if (guess[i] === code[i]) {
            fullMatch++;
        }
    } 
    let anyMatch = 0;
    let code1 = [...code];
    for (let i=0; i < 4; i++) {
        for (let j=code1.length; j >= 0; j--) {
            if (guess[i] === code1[j]) {
                anyMatch++;
                var a = code1[code1.length-1];
                code1[code1.length-1] = code1[j];
                code1[j] = a;
                code1.pop();
                break; 
            }
        } 
    }
    if (fullMatch === 4) {
        return [0, 0, 0, 0];
    } else if (fullMatch === 3) {
        if (anyMatch === 4) {
            return [0, 0, 0, 3];
        } else {
            return [0, 0, 0, 7];
        }
    } else if (fullMatch === 2) {
        if (anyMatch === 4) {
            return [0, 0, 3, 3];
        } else if (anyMatch === 3) {
            return [0, 0, 3, 7];
        } else {
            return [0, 0, 7, 7];
        }
    } else if (fullMatch === 1) {
        if (anyMatch === 4) {
            return [0, 3, 3, 3];
        } else if (anyMatch === 3) {
            return [0, 3, 3, 7];
        } else if (anyMatch === 2) {
            return [0, 3, 7, 7];
        } else {
            return [0, 7, 7, 7];
        }
    } else if (fullMatch === 0) {
        if (anyMatch === 4) {
            return [3, 3, 3, 3];
        } else if (anyMatch === 3) {
            return [3, 3, 3, 7];
        } else if (anyMatch === 2) {
            return [3, 3, 7, 7];
        } else if (anyMatch === 1) {
            return [3, 7, 7, 7];
        } else {
            return [7, 7, 7, 7];
        }
    }
    return [7, 7, 7, 7];
}

function App(){
    const [slots, setSlots] = useState(slots0);
    const [row, setRow] = useState(row0);
    const [matches, setMatches] = useState(matches0);
    const [code, setCode] = useState(code0);

    const Ball = ({idx}) => {
        const clickHandler1 = (idx) => {
            chosen = idx;
        } 
        return (
            <span className="dot" onClick={() => clickHandler1(idx)} style={{backgroundColor: colors[idx]}}>
            </span>
        );
    };

    const Slot = ({idx}) => {
        const clickHandler2 = (idx) => {
            if (Math.floor(idx/4) === row) {
                let newSlots = [...slots];
                newSlots[idx] = chosen;
                setSlots(newSlots);
            }
        } 
        return (
            <span className="dot" onClick={() => clickHandler2(idx)} style={{backgroundColor: colors[slots[idx]]}}>
            </span>
        );
    };

    const Match = ({idx}) => {
        return (
            <span className="smalldot" style={{backgroundColor: colors[matches[idx]]}}>
            </span>
        );
    };

    const Buttons = ({name}) => {
        const buttonHandler = (name) => {
            if (name === 'Check' && row >= 0) {
                if (slots[row*4]<6 && slots[row*4+1]<6 && slots[row*4+2]<6 && slots[row*4+3]<6) {
                    let guessMatch = matchCheck(slots.slice(row*4, (row+1)*4), code);
                    let newMatches = [...matches];
                    newMatches[row*4] = guessMatch[0];
                    newMatches[row*4+1] = guessMatch[1];
                    newMatches[row*4+2] = guessMatch[2];
                    newMatches[row*4+3] = guessMatch[3];
                    setMatches(newMatches);    
                    if (guessMatch[0] === 0 && guessMatch[1] === 0 && guessMatch[2] === 0 && guessMatch[3] === 0) {
                        alert('You won!')
                        setRow(-1);
                    } else {
                        if (row === 0) {
                            setRow(row-1);
                            alert('Game over!')
                        } else {
                            setRow(row-1);
                        }
                    }
                } else {
                    alert('Fill all slots.')
                }
            } else if (name !== 'Check') {
                setSlots(slots0);
                setRow(row0);
                let newCode = randomCode();
                setCode(newCode);
                setMatches(matches0);
            }
        }
        return (
            <button className="button" onClick={() => buttonHandler(name)}>{name}</button>
        );
    }

    return (
        <section className="board">
            <div className="slottery">
                {slots.map((slot, index) => {
                    return <Slot key={index} idx={index}></Slot>
                })}
            </div>
            <div className="menu">
                {buttons.map((button) => {
                    return <Buttons key={button.id} name={button.name}></Buttons>
                })}
            </div>
            <div className="colorPalette">
                {balls.map((ball) => {
                    return <Ball key={ball.idx} idx={ball.idx}></Ball>
                })}
            </div>
            <div className="match">
                {matches.map((match, index) => {
                    return <Match key={index} idx={index}></Match>
                })}
            </div>
            <div className="header">
                <h1>Mastermind</h1>
            </div>
        </section>
    );
};


export default App;